<template>
  <v-footer
    class="BG__Color__Footer ppy-5"
    padless
  >
  <v-row
  justify="center"
  >
    <v-row
      gutters="20"
      class="d-flex justify-space-between"
      style="max-width: 1150px"
    >
    <template class="hidden-sm-and-down">
      <ul
        v-for="link in links"
        :key="link"
        text
        rounded
        class="my-2 Footer__Menus"
        id="menuID"
      >
        <template v-if="link.children">
            <h4 href="javascript:void(0)" style="font-weight: normal;"><span><b>{{ link.name }}</b></span>
                <ul class="pl-0 pt-5">
                    <li v-for="{name, href, index} in link.children" :key="index">
                        <a :href="href">{{ name }}</a>
                    </li>
                </ul>
            </h4>
        </template>
        <template v-else>
            <a :href="href">{{ link.name }}</a>
        </template>
       </ul>
      </template>


      <div class="hidden-md-and-up mw-100">
        <v-menu offset-y v-for="link in links" :key="link">
            <template v-slot:activator="{ on }">
                <v-btn
                v-on="on"
                class="menu_Style"
                text
                >
                <span>
                {{ link.name }}
                </span>
                <div class="footerCategory--header-arrow" style="transform: rotate(-90deg);"><svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" viewBox="0 0 36 36"><g fill="none" fill-rule="evenodd"><g stroke="#3F3F3F" stroke-linecap="round" stroke-width="2"><path d="M20.1 11.304l-7.082 6.76M20.1 25.145l-7.082-6.76"></path></g></g></svg></div>
                </v-btn>
            </template>
            <template v-if="link.children">
            <v-list>
                <v-list-item
                v-for="{name, href, index} in link.children" :key="index"
                >
                <v-list-item-title><a :href="href">{{ name }}</a></v-list-item-title>
                </v-list-item>
            </v-list>
            </template>
        </v-menu>
      </div>

      <div class="search__Mobile">
          <v-col class="d-flex py-0" cols="12">
            <v-autocomplete
                v-model="countries[0]"
                :items="countries"
                solo
                outlined
                flat
                height="45"
                no-filter
                class="autoCompleteFooter"
          ></v-autocomplete>
        </v-col>
        <v-col class="d-flex py-0" cols="12">
            <v-icon v-for="icon in icons" :key="icon" class="mr-3" color="black">
                {{ icon }}
            </v-icon>
        </v-col>
      </div>
      
      <v-col
        class="BG__Color__Footer text-center mt-5"
        cols="12"
      >
        <v-row>
            <v-col class="d-flex PlayStore__Button pr-0" cols="12" md="4" sm="12">
                <a href="#">
                    <img :src="appStoreApple" alt="Apple Play Store">
                </a>
                <a href="#">
                    <img :src="appStoreGoogle" alt="Google Play Store">
                </a>
            </v-col>
            <v-col class="ml-auto" md="8" sm="12" cols="12">
                <div class="footer_Copyright">
                <p>© Badiapp Technologies, S.L.</p>
                <p class="footer__Copyright__Mobile">This site is protected by reCAPTCHA and the Google Privacy Policy and <a href="javascript:void(0)">Terms of Service</a> apply.</p>
                </div>
            </v-col>
        </v-row>
      </v-col>
    </v-row>
    </v-row>
  </v-footer>
</template>
<script>
  export default {
    data: () => ({
      appStoreApple: './assets/img/apple-store.svg',
      appStoreGoogle: './assets/img/google-play.svg',
      countries: ['English (UK)', 'English (US)', 'Franch'],
      errorMessages: '',
      icons: [
        'fab fa-twitter',
        'fab fa-facebook-square',
        'fab fa-instagram',
        'fab fa-linkedin',
      ],
      links: [
        {
            name: 'BADI',
            children: [
                {
                    name: 'Jobs',
                    href: '#'
                },
                {
                    name: 'Safety Tips',
                    href: '#'
                }
            ]
        },
        {
            name: 'MEDIA',
            children: [
                {
                    name: 'Press Kit',
                    href: '#'
                },
                {
                    name: 'Blog',
                    href: '#'
                }
            ]
        },
        {
            name: 'CONTACT',
            children: [
                {
                    name: 'FAQs',
                    href: '#'
                },
                {
                    name: 'Customer Care',
                    href: '#'
                },
                {
                    name: 'Partnership',
                    href: '#'
                },
                {
                    name: 'Press Inquiries',
                    href: '#'
                }
            ]
        },
        {
            name: 'LEGAL',
            children: [
                {
                    name: 'Terms and conditions',
                    href: '#'
                },
                {
                    name: 'Privacy policy',
                    href: '#'
                },
                {
                    name: 'Booking policy',
                    href: '#'
                }
            ]
        }
      ],
    }),
  }
</script>
<style>
.v-text-field.v-text-field--enclosed .v-text-field__details {
    display: none;
}
</style>
<style scoped>
.footer_Copyright p {
    float: right;
    margin-bottom: 0;
}
.autoCompleteFooter input {
    cursor: pointer;
}
.BG__Color__Footer {
    background-color: #F3F1E9!important;
}
.Footer__Menus {
    display: flex;
}
.Footer__Menus li {
    list-style-type: none;
}
.ppy-5 {
    padding: 3% 0;
}
a {
    text-decoration: none;
    color: black!important;
}
a:hover {
    text-decoration: underline;
}
li {
    padding: 5% 0;
}
b{
    padding: 5% 0;
    text-decoration: none!important;
}
@media screen and (max-width: 959px){
    .mw-100 {
        width: 100%;
    }

    .menu_Style {
        display: block;
        margin-left: 10%;
        margin-right: 10%;
        width: 80%;
        border-top: 1px solid #DAD8D1;
        border-bottom: 1px solid #DAD8D1;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    #menuID {
        display: none!important;
    }

    .menu_Style span {
        display: block;
        text-align: left;
        width: 100%;
    }

    .search__Mobile {
        margin-top: 5%;
        width: 100%;
        margin-left: 7%;
        margin-right: 7%;
    }

    .justify__Content__Mobile {
        -webkit-box-pack: center !important;
        justify-content: center!important;
    }

    .footer_Copyright p {
        float: left;
        margin-left: 5%;
        margin-bottom: 0;
        padding-right: 5%;
    }

    .PlayStore__Button {
        margin-left: 7%;
    }

    .footer__Copyright__Mobile {
        margin-top: 10px;
        font-size: 12px;
        text-align-last: left;
        text-align: left;
    }
}
</style>