<template>
<v-card
    flat
    tile
    style=""
    class="Toolbar__Custom__Style"
  >
    <v-toolbar
        flat
    >
    
<div class="d-flex justify-space-between w-100__Mobile">

<div class="hidden-md-and-up">
  <MobileMenu />
</div>




        

  <div class="ml-auto">
    <v-toolbar-title
      class="mr-2"
    >
      <b>
      VueAPP
      </b>
    </v-toolbar-title>
  </div>

 


  <div class="ml-auto hidden-md-and-up">

    <MobileSearch />
    <!-- <v-btn 
      text
      >
      <img :src="searchImage" alt="Search Icon for mobile">
    </v-btn> -->
  </div>

   <v-spacer class="hidden-sm-and-down"></v-spacer>

</div>

        <v-col cols="12" sm="6" md="3" class="hidden-sm-and-down">
          <v-autocomplete
            v-model="select"
            :loading="loading"
            :items="items"
            :search-input.sync="search"
            chips
            clearable
            hide-details
            hide-selected
            append-icon=""
            solo
            item-text="name"
            item-value="symbol"
            label="Where do you want to live..."
            background-color="white"
            prepend-inner-icon="mdi-magnify"
            class="widthOfSearch"
            style="width: 350px!important;"
            >
            <template v-slot:no-data>
                <v-list-item>
                <v-list-item-title>
                    Search for your favorite
                    <strong>Room</strong>
                </v-list-item-title>
                </v-list-item>
            </template>
        </v-autocomplete>
        </v-col>
       

        <v-spacer></v-spacer>
        <v-btn
            class="rentButton mr-2 hidden-sm-and-down"
            outlined
        >
          Rent out your room
        </v-btn>

        <v-btn
            v-for="(link, i) in links"
            :key="i"
            :to="link.to"
            flat
            text
            class="ml-0 hidden-sm-and-down mr-2"
            @click="onClick($event, item)"
        >
        <div v-if="i==0" class="rent-btn">
            {{ link.title }}
        </div>
        <div v-else>
            {{ link.title }}
        </div>
        </v-btn>



        

    </v-toolbar>


    
  </v-card>
</template>

<script>
import MobileMenu from './partials/MobileMenu'
import MobileSearch from './partials/MobileSearch'
  export default {
    data() {
      return {
        drawer: false,
        group: null,
        links: [
          {
            title: 'Register',
            to: '#register'
          },
          {
            title: 'Log in',
            to: '#login'
          }
        ],
        mobLinks: [
          {
            title: 'Rent out your room',
            to: '#rent'
          },
          {
            title: 'Register',
            to: '#register'
          },
          {
            title: 'Log in',
            to: '#login'
          }
        ],
        loading: false,
        items: [],
        search: null,
        select: null,
        states: [
          'Alabama',
          'Alaska',
          'American Samoa',
          'Arizona',
          'Arkansas',
          'California',
          'Colorado',
          'Connecticut',
          'Delaware',
          'District of Columbia',
          'Federated States of Micronesia',
          'Florida',
          'Georgia',
          'Guam',
          'Hawaii',
          'Idaho',
          'Illinois',
          'Indiana',
          'Iowa',
          'Kansas',
          'Kentucky',
          'Louisiana',
          'Maine',
          'Marshall Islands',
          'Maryland',
          'Massachusetts',
          'Michigan',
          'Minnesota',
          'Mississippi',
          'Missouri',
          'Montana',
          'Nebraska',
          'Nevada',
          'New Hampshire',
          'New Jersey',
          'New Mexico',
          'New York',
          'North Carolina',
          'North Dakota',
          'Northern Mariana Islands',
          'Ohio',
          'Oklahoma',
          'Oregon',
          'Palau',
          'Pennsylvania',
          'Puerto Rico',
          'Rhode Island',
          'South Carolina',
          'South Dakota',
          'Tennessee',
          'Texas',
          'Utah',
          'Vermont',
          'Virgin Island',
          'Virginia',
          'Washington',
          'West Virginia',
          'Wisconsin',
          'Wyoming',
        ],
      }
    },
    watch: {
      search (val) {
        val && val !== this.select && this.querySelections(val)
      },
      group () {
        this.drawer = false
      },
    },
    methods: {
      querySelections (v) {
        this.loading = true
        // Simulated ajax query
        setTimeout(() => {
          this.items = this.states.filter(e => {
            return (e || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
          })
          this.loading = false
        }, 500)
      },
    },
    components: {
      MobileMenu,
      MobileSearch
    }
  }
</script>
<style scoped>
.Toolbar__Custom__Style {
  border-bottom: 1px solid #f0f0f0; 
  border-color: #F0F0F0 !important;
  padding: 5px 0;
  position: fixed;
  width: 100%;
  z-index: 999;
}
.hideMobile {
    display: none;
}
.card-bg {
    padding: 5px 0;
}
.pt-10 {
  padding-top: 10px; 
}

.mr-2 {
  margin-right: 2rem; 
}
.widthOfSearch {
  width: 350px!important;
}
.rentButton {
  border: 1px solid #1E3255;
  border-radius: 4px;
  min-height: 50px!important;
}

.searchBar {
  width: 300px;
}

.w-50 {
  width: 50%;
}

.w-100 {
  width: 100%;
}

@media screen and (max-width: 960px){
  .w-100__Mobile {
    width: 100%;
  }
}
</style>
<style>
.v-btn {
  text-transform: unset!important;
}
</style>
